<template>
  <div class="personal">
    <div class="nav">
      <el-tag effect="plain" :class="{active:personalData==typeActive}" @click="menuChange('personalData')">个人资料</el-tag>
      <el-tag effect="plain" :class="{active:modify==typeActive}" @click="menuChange('modify')">修改密码</el-tag>
    </div>
    <div :is="currentView"></div>
  </div>
</template>

<script>
import personalData from '@/views/personal/data'
import modify from '@/views/personal/modify'

export default {
  components: {
    personalData,
    modify
  },
  data() {
    return {
      personalData: 'personalData',
      modify: 'modify',
      typeActive: 'personalData',
      currentView: 'personalData'
    }
  },
  created() {},
  methods: {
    menuChange (menu) {
      this.currentView = menu
      this.typeActive = menu
    }
  }
}
</script>
<style lang="scss" scoped>
  .active {
          background-color: #409eff;
            color: #fff;
        }
  .personal{
    margin: 0 auto;
    width: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
